import React, { useState } from "react";
import { Button, Icon, Link, Slider, TextField } from "@mui/material";
import styled from "@emotion/styled";
import { QRCode } from "react-qrcode-logo";
import html2canvas from "html2canvas";
import { logo_base64, logo__with_text_base64 } from "../service/constants";
import AdsClickIcon from "@mui/icons-material/AdsClick";
import Avatar from "@mui/material/Avatar";
interface QRFormPageProps {}

export const QRFormPage: React.FC<QRFormPageProps> = () => {
  const [errorText, setErrorText] = React.useState<string>("");
  const [mobile, setMobile] = React.useState<string>("");
  const [qrURL, setQrURL] = React.useState<string>("rescueqr");
  const [qrWidth, setQrWidth] = React.useState<any>(175);
  const [activeStep, setActiveStep] = React.useState(0);
  const [showHowTo, setShowHowTo] = React.useState(false);
  const [logoOp, setLogoOp] = React.useState(0.6);

  const [smallLogoWidth, setSmallLogoWidth] = React.useState<any>("10");
  const [linktext, setLinkText] = React.useState<any>("25");
  const [customQRDiv, setCustomQRDiv] = React.useState<any>("78");

  const execQR = (value: any) => {
    if (value) {
      setQrURL(value);
      setActiveStep(activeStep + 1);
    }
  };

  const upDateLogoOp = (scale: number) => {
    if (scale <= 40) {
      setLogoOp(0.4);
    }
    if (scale > 100) {
      setLogoOp(0.8);
    }
  };

  const OnChangeHandler = (event: any) => {
    if (event && isNaN(event.target.value)) {
      setErrorText("Please enter only numbers!");
      event.preventDefault();
      setMobile("");
    } else {
      setErrorText("");
      setMobile(event.target.value);
      execQR(event.target.value);
    }
  };
  const handleDownload = () => {
    html2canvas(document.querySelector("#qr_image") as any).then(function (
      canvas
    ) {
      const link = document.createElement("a");
      link.download = "rescue-qr.png";
      link.href = canvas.toDataURL();
      link.click();
    });
  };
  const updateLinkImageChange = (newValue: number | number[]) => {
    console.log("newValue::", newValue);
    switch (newValue as number) {
      case 10:
        setSmallLogoWidth(6);
        setLinkText(10);
        setCustomQRDiv(34);
        break;
      case 20:
        setSmallLogoWidth(6);
        setLinkText(12);
        setCustomQRDiv(36);
        break;
      case 30:
        setSmallLogoWidth(8);
        setLinkText(14);
        setCustomQRDiv(40);
        break;
      case 40:
        setSmallLogoWidth(9);
        setLinkText(16);
        setCustomQRDiv(44);
        break;
      case 50:
        setSmallLogoWidth(8);
        setLinkText(18);
        setCustomQRDiv(48);
        break;
      case 60:
        setSmallLogoWidth(8);
        setLinkText(18);
        setCustomQRDiv(52);
        break;
      case 70:
        setSmallLogoWidth(8);
        setLinkText(20);
        setCustomQRDiv(56);
        break;
      case 80:
        setSmallLogoWidth(8);
        setLinkText(20);
        setCustomQRDiv(60);
        break;
      case 90:
        setSmallLogoWidth(8);
        setLinkText(22);
        setCustomQRDiv(64);
        break;
      case 100:
        setSmallLogoWidth(8);
        setLinkText(23);
        setCustomQRDiv(68);
        break;
      case 110:
        setSmallLogoWidth(8);
        setLinkText(24);
        setCustomQRDiv(72);
        break;
      case 120:
        setSmallLogoWidth(8);
        setLinkText(25);
        setCustomQRDiv(76);
        break;
      case 130:
        setSmallLogoWidth(8);
        setLinkText(25);
        setCustomQRDiv(80);
        break;
    }
  };

  const handleQrSizeChange = (event: Event, newValue: number | number[]) => {
    console.log("handleQrSizeChange :: ", newValue);
    upDateLogoOp(newValue as number);
    setQrWidth((newValue as number) + 50);
    updateLinkImageChange(newValue);
  };

  return (
    <>
      <div className="qrPanel">
        <TextField
          required
          error={errorText ? true : false}
          id="mobile"
          label="Mobile"
          name="mobile"
          style={{ marginLeft: "10%", width: "80%" }}
          autoComplete="given-name"
          variant="standard"
          onChange={(e) => OnChangeHandler(e)}
          helperText={errorText}
        />
        <QRSlider
          size="small"
          defaultValue={125}
          aria-label="Small"
          valueLabelDisplay="auto"
          onChange={handleQrSizeChange}
          step={10}
          marks
          min={30}
          max={125}
        />
        <QRPanel>
          <div
            id="qr_image"
            style={{
              width: customQRDiv + "%",
              border: "solid 2px #000000",
              borderStyle: "dashed",
              borderRadius: "25px",
              padding: "1%",
              margin: "auto"
            }}
          >
            <CustomQRCodeDiv>
              <QRCode
                size={qrWidth}
                eyeColor={[
                  {
                    // top/left eye
                    outer: "#0ed2b3",
                    inner: "#ffc048"
                  },
                  { outer: "#0ed2b3", inner: "#ffc048" }, // top/right eye
                  { outer: "#0ed2b3", inner: "#ffc048" } // bottom/left eye
                ]}
                removeQrCodeBehindLogo={false}
                qrStyle="dots"
                logoHeight={60}
                logoWidth={60}
                logoOpacity={logoOp}
                value={qrURL}
                quietZone={10}
                logoImage={logo__with_text_base64}
              />
            </CustomQRCodeDiv>

            <CustomParagraph>
              <img
                alt="RescueQR"
                width={smallLogoWidth + "%"}
                src={window.location.origin + "/images/logo/logo.png"}
                title="rescueqr.in"
              />
              <span style={{ fontSize: linktext + "px" }}>rescueqr.in</span>
            </CustomParagraph>
          </div>

          <Button
            variant="outlined"
            sx={{ mt: 2, marginLeft: "20%" }}
            onClick={handleDownload}
            disabled={errorText ? true : false}
          >
            Download Rescue QR
          </Button>
        </QRPanel>
      </div>
    </>
  );
};

export default QRFormPage;

export const QRPanel = styled.div``;

export const QRSlider = styled(Slider)`
  width: 80% !important;
  margin-left: 10%;
  height: 20px !important;
`;

export const CustomLogo = styled.img`
  width: 15%;
  float: left;
  margin-left: 5%;
`;

export const CustomBgLogo = styled.img`
  width: 75%;
  float: left;
  margin-left: 14%;
`;

export const CustomHowToImage = styled.img`
  width: 100%;
`;

export const CustomTitle = styled.h1`
  float: right;
`;

export const CustomParagraph = styled.p`
  text-align: center;
  font-weight: bold;
  position: relative;
  left: 1px;
  top: -12px;
`;

export const CustomQRCodeDiv = styled.div`
  margin-top: 3%;
  margin-left: 10%;
`;
